$primaryColor: #146EF5;
$secondaryColor: #FF7A00;
$titleColor: #070127;
$contentColor: #485781;

.bPlLoading {
	.loadingContent {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 15px;

		&.horizontal {
			flex-direction: row;
		}

		.loadingSpinner {
			width: 2rem;
			height: 2rem;
			border: 4px solid $primaryColor;
			border-top-color: transparent;
			border-radius: 50%;
			animation: bPlLoadingSpin 1s linear infinite;
		}

		.loadingText {
			color: $contentColor;
			font-size: 18px;
		}
	}

	@keyframes bPlLoadingSpin {
		0% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(360deg);
		}
	}
}