$primaryColor: #146EF5;
$secondaryColor: #FF7A00;
$titleColor: #070127;
$contentColor: #485781;

.bPlDashboardListDemos {
	max-width: 100%;
	min-height: 600px;
	height: calc(100dvh - 160px - var(--wp-admin--admin-bar--height, 32px));
	background: #fff;
	display: flex;
	border-radius: 20px;
	overflow: hidden;
	box-shadow: 0 20px 25px -5px #0000001a, 0 8px 10px -6px #0000001a;

	.sidebar {
		min-width: 320px;
		color: #fff;
		background: $titleColor;
		display: flex;
		flex-direction: column;
		padding: 20px 0;

		.sidebarHeader {
			margin-bottom: 15px;
			padding: 0 15px;

			h2 {
				font-size: 28px;
				color: #fff;
				margin-bottom: 8px;
			}

			p {
				font-size: 15px;
				color: #9ca3af;
				margin: 0;
			}

			.bPlDashboardButtons {
				display: none;
			}
		}

		.sidebarList {
			flex: 1;
			display: flex;
			flex-direction: column;
			gap: 12px;
			padding: 0 15px;
			overflow: auto;

			button {
				width: 100%;
				text-align: left;
				color: #d1d5db;
				background-color: #1f2937;
				display: inline-flex;
				align-items: flex-start;
				gap: 12px;
				padding: 15px;
				border: none;
				border-radius: 8px;
				margin: 0;
				cursor: pointer;
				transition: all .2s cubic-bezier(.4, 0, .2, 1);

				.iconBox {
					width: 30px;
					height: 30px;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 5px;
					color: #eff2f6;
					background-color: #374151;

					svg {
						fill: #eff2f6;
					}

					svg {
						width: 15px;
						height: 15px;
					}
				}

				.texts {
					flex: 1;

					h4 {
						font-size: 14px;
						font-weight: 500;
						color: #eff2f6;
						margin: 0 0 4px 0;
					}

					p {
						font-size: 12px;
						font-weight: 400;
						color: #8da2bf;
						margin: 0;
					}

					.category {
						font-size: 12px;
						font-weight: 400;
						display: inline-block;
						color: #adbcd1;
						background-color: #374151;
						padding: 4px 08px;
						border-radius: 100px;
						margin-top: 08px;
					}
				}

				&:hover {
					color: white;
					background-color: #374151;

					.iconBox {
						color: #fff;
						background-color: #4b5563;

						svg {
							fill: #fff;
						}
					}

					.texts {
						h4 {
							color: white;
						}

						p {
							color: #ced7e3;
						}

						.category {
							color: #ced7e3;
							background-color: #4b5563;
						}
					}
				}

				&.active {
					color: white;
					background-color: $primaryColor;
					box-shadow: 0 10px 15px -3px #0000001a;

					.iconBox {
						color: #fff;
						background-color: var(--theme-color-400);

						svg {
							fill: #fff;
						}
					}

					.texts {
						h4 {
							color: #fff;
						}

						p {
							color: var(--theme-color-100);
						}

						.category {
							color: var(--theme-color-100);
							background-color: var(--theme-color-400);
						}
					}
				}
			}
		}

		.sidebarList::-webkit-scrollbar {
			width: 5px;
			height: 10px;
			background-color: #0000;
		}

		.sidebarList::-webkit-scrollbar-track {
			border-radius: 5px;
			background-color: #0000;
		}

		.sidebarList::-webkit-scrollbar-thumb {
			border-radius: 5px;
			background-color: #b9b9b9;
		}
	}

	.main {
		flex: 1 1 0%;
		display: flex;
		flex-direction: column;
		overflow: hidden;

		.mainHeader {
			padding: 18px 15px;
			background-color: #f9fafb;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #e5e7eb;

			.headerInfo {
				h3 {
					font-size: 19px;
					font-weight: 600;
					margin: 0 0 08px 0;
				}

				p {
					font-size: 14px;
					margin: 0;
				}
			}
		}

		.canvas {
			flex: 1 1 0%;
			background: #fff;
			display: flex;
			flex-direction: column;
			position: relative;
			border: 2px solid #e5e7eb;
			border-radius: 08px;
			overflow: hidden;

			.bPlLoading {
				position: absolute;
				inset: 0;
				background-color: rgba(255, 255, 255, 0.75);
				display: flex;
				align-items: center;
				justify-content: center;
				z-index: 10;
			}

			iframe {
				flex: 1 1 0%;
				width: 100%;
				height: 100%;
				vertical-align: middle;
				border: none;
			}

			.canvasImg {
				text-align: center;
				width: calc(100% - 40px);
				height: calc(100% - 40px);
				margin: auto;
				overflow: hidden;

				img {
					max-width: 100%;
					transition: all 1s ease;
				}
			}
		}

		.mainFooter {
			font-size: 14px;
			color: #4b5563;
			background-color: #f9fafb;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 15px;
			border-top: 1px solid #e5e7eb;

			.status {
				display: flex;
				align-items: center;

				.statusDot {
					width: 08px;
					height: 08px;
					background-color: #22c55e;
					border-radius: 9999px;
					margin-right: 08px;
				}
			}

			.demoLink {
				font-weight: bold;
				display: flex;
				align-items: center;
				justify-content: center;
				gap: 8px;
				text-decoration: underline !important;
				color: $primaryColor;

				svg {
					width: 12px;
					height: 12px;
				}
			}
		}
	}
}

@media only screen and (max-width: 1024px) {
	.bPlDashboardListDemos {
		flex-direction: column;

		.sidebar {
			width: 100%;
			padding-bottom: 0;

			.sidebarHeader {
				display: flex;
				justify-content: space-between;
				flex-wrap: wrap;
				gap: 15px;

				.bPlDashboardButtons {
					display: flex;
				}
			}

			.sidebarList {
				max-width: 100%;
				flex-direction: row;
				flex-wrap: nowrap;
				padding: 0 8px 16px 8px;

				button {
					width: auto;
					min-width: 200px;
					align-items: center;
					padding: 12px 16px;

					.texts {
						h4 {
							margin: 0;
						}

						p {
							display: none;
						}

						.category {
							display: none;
						}
					}

					.externalIcon {
						display: none;
					}
				}
			}
		}

		.main {
			width: 100%;

			.mainHeader {
				display: none;
			}
		}
	}
}